import React from "react"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"

import "tabler-react/dist/Tabler.css";

import { Page, Card, Grid, GalleryCard, Form } from "tabler-react";
  
class PatientInfo extends React.Component {
  
  constructor ({ location }){
    super();
    if (location.search)
      this.search = new URLSearchParams(location.search).get('q');
    else
      this.search = "";
  }

	componentWillMount() {
		this.setState({
		  query: this.search,
      rows: []
		});
	}
	componentDidMount() {
    if (this.state.query.length > 0)
      this.fetchOnline(this.state.query);
	}
  
  fetchOnline(query) {
      console.log(query);          
      var _this = this;
    
      axios
        .get("https://script.google.com/macros/s/AKfycbzr0R-IGH3xbXPcIs81BF1q_oe_6SQ34t7F1GpZxsXMykTlXA/exec?q=" + query)
        .then(({ data }) => {
                    
          var rx = /callback\((.*)\);/g;
          var arr = rx.exec(data);
          data = JSON.parse(arr[1]);
          
          if (!data)
            return; 
          
          data = data.rows;
          
          if (data)
            _this.setState({ loading: false, rows: data });
          else
            _this.setState({ loading: false, rows: [] });
          
          console.log(data);

        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
    }
    
  handleInputChange(event) {
    var _this = this;
    var searchText = event.target.value;
    if (this.timeout) clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      _this.fetchOnline(searchText);
    }, 600);
  }
    
	render() {
    const options = (
      <React.Fragment>
        <Form.Input 
          icon="search" 
          placeholder="搜尋資料..."
          value={this.search}
          onChange={event => this.handleInputChange(event)}
        />
      </React.Fragment>
    );
  return (
  <Layout>
    <SEO title="健康資訊搜尋器" />
    <Page.Content>
      <Page.Header
          title="健康資訊搜尋器"
          subTitle={ this.state.rows ? "我們找到"+this.state.rows.length+"份資料" : "" }
          options={options}
        />
      <p>為你搜尋醫管局、衛生署、各大醫院及醫學院提供的資料。不定時更新。</p>
        <Grid.Row className="row-cards">
          {this.state.rows.map((item, key) => (
            <Grid.Col sm={6} lg={4} key={key}>
              <a 
                href={item[0]}
                target="_blank" rel="noopener noreferrer">
                <GalleryCard>
                  <GalleryCard.Image
                    src={`data:image/png;base64,${item[1].replace(/["']/g, "")}`}
                    alt={item[2]}
                  />
                  <GalleryCard.Footer>
                    <GalleryCard.Details
                      fullName={item[2]}
                      dateString={item[0]}
                    />
                  </GalleryCard.Footer>
                </GalleryCard>
              </a>
            </Grid.Col>
          ))}
        </Grid.Row>
    </Page.Content>
  </Layout>
  );
  }
}

export default PatientInfo
